function SearchElement(props) {
  function nameInput(e) {
    props.setNickname(e.target.value);
  }

  // const updateChange = (e) => {
  //   let data = e.target.value;
  //   let filterData = mydata.filter((i) =>
  //     i.name.toLowerCase().includes(data.toLowerCase())
  //   );
  //   if (data.length === 0) {
  //     filterData = [];
  //   }
  //   setSearch(filterData);
  // };

  function clickBtn() {
    props.setBtnClick(true);
  }

  return (
    <Fragment>
      <table className={props.device + '_search_div'}>
        <tbody>
          <tr>
            <td>
              <input
                type="search"
                className={props.device + '_search_guideline'}
                placeholder={props.nickname ? props.nickname : ' .'}
                maxLength="20"
                size="30"
                onChange={nameInput}
              ></input>
            </td>
            <td>
              <img className="zoom_icon" src={zoom} alt="" onClick={clickBtn}></img>
            </td>
          </tr>
        </tbody>
      </table>
    </Fragment>
  );
}

export default SearchElement;
